.profile__name {
  margin: 23px 0 7px;
  padding: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #FFFFFF;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 618px) {
  .profile__name {
    margin: 0 auto 7px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}
